export interface SambaConfig {
  address: string;
  username: string;
  password?: string;
  tmpDirectory: string;
  domain?: string;
  directory?: string;
  maxProtocol?: string;
  port?: string;
  timeout?: string;
  maskCmd?: boolean;
}

export function isSambaConfig(obj: SambaConfig): obj is SambaConfig {
  return (
    typeof obj.address === 'string' &&
    typeof obj.username === 'string' &&
    typeof obj.tmpDirectory === 'string' &&
    (typeof obj.password === 'string' || obj.password === undefined) &&
    (typeof obj.domain === 'string' || obj.domain === undefined) &&
    (typeof obj.directory === 'string' || obj.directory === undefined) &&
    (typeof obj.maxProtocol === 'string' || obj.maxProtocol === undefined) &&
    (typeof obj.port === 'string' || obj.port === undefined) &&
    (typeof obj.timeout === 'string' || obj.timeout === undefined) &&
    (typeof obj.maskCmd === 'boolean' || obj.maskCmd === undefined)
  )
}
